@use "variables/variables" as *;
 
 
 html, html[data-theme="dark"] {
        --yellow : #fff0a6;
        --pink : #fc9b9b;
        --purple : #c6b4f0;
        --green : #00cdaf;
        --blue :#00a9ff;
        --blue3 : #62c8f3;

        --grey : #888888;
        --grey2 : #4e5d62;
        --grey3: #dee5e7;
        --warm: #fefeec;
        --code-background-color: #161b22;
    }

    html[data-theme="light"] {
        --yellow : #c5a332;
        --pink : #d52753;
        --purple : #ce33c0;
        --green : #00cdaf;
        --blue : #275fe4;
        --blue3 : #275fe4;

        --grey : #888888;
        --grey2 : #4e5d62;
        --grey3: #dee5e7;
        --warm: #fefeec;
        --code-background-color: #fafafa;
  }
  
@media (prefers-color-scheme: dark) {
    html, html[data-theme="dark"] {
        --yellow : #fff0a6;
        --pink : #fc9b9b;
        --purple : #c6b4f0;
        --green : #00cdaf;
        --blue :#00a9ff;
        --blue3 : #62c8f3;

        --grey : #888888;
        --grey2 : #4e5d62;
        --grey3: #dee5e7;
        --warm: #fefeec;
        --code-background-color: #161b22;
    }

    html[data-theme="light"] {
        --yellow : #c5a332;
        --pink : #d52753;
        --purple : #ce33c0;
        --green : #00cdaf;
        --blue : #275fe4;
        --blue3 : #275fe4;

        --grey : #888888;
        --grey2 : #4e5d62;
        --grey3: #dee5e7;
        --warm: #fefeec;
        --code-background-color: #fafafa;
  } 
}



.highlight .gt {
    color: var(--grey3);
    background-color: var(--grey2);
}

.highlight .gl {
    color: var(--grey3);
    background-color: var(--grey2);
}

code {
    tab-size: 4;
}

.highlight table td {
    padding: 0px;
}

.highlight table pre {
    margin: 0;
}

.highlight {
    color: var(--text-color);
    background-color: var(--code-background-color);
}



.highlight .gp {
    color: var(--blue);
    font-weight: bold;
}

