@use "variables/variables" as *;

.post_container {
    display: flex;
    flex-grow: 1;
     max-width: -webkit-calc(#{$content-width-home} + (#{$spacing-unit} * 2));
    max-width: calc(#{$content-width-home} + (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @media screen and (max-width: 600px) {
        max-width: -webkit-calc(#{$content-width} + (#{$spacing-unit} * 2));
        max-width: calc(#{$content-width} + (#{$spacing-unit} * 2));
        padding-right: $spacing-unit;
        padding-left: $spacing-unit;
    }
}



.post_item_left {
    // background-color: #aea;
    // max-width: -webkit-calc(#{$content-width-home} *0.7 + (#{$spacing-unit} * 2));
    // max-width: calc(#{$content-width-home} *0.7 + (#{$spacing-unit} * 2));
    width: 70%;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    border-right: 1px solid var(--color-1);
    @extend %clearfix;

    @media screen and (max-width: 600px) {
        // max-width: -webkit-calc(#{$content-width} + (#{$spacing-unit} * 2));
        // max-width: calc(#{$content-width} + (#{$spacing-unit} * 2));
        width: 100%;
        padding-right: 0;
        padding-left: 0;
        border-right: 0px solid var(--color-1);
    }
}

.post_item_right {
    // background-color: #eea;
    // width: -webkit-calc(#{$content-width-home} *0.3 + (#{$spacing-unit} * 2));
    // width: calc(#{$content-width-home} *0.3 + (#{$spacing-unit} * 2));
    width: 30%;;
    margin-right: auto;

    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @media screen and (max-width: 600px) {
        // max-width: -webkit-calc(#{$content-width} + (#{$spacing-unit} * 2));
        // max-width: calc(#{$content-width} + (#{$spacing-unit} * 2));
        padding-right: 0;
        padding-left: 0;
    }
}




// ------------------------------------------------------------------








.post-list {
    margin-top: 2.5rem;
    margin-left: 0;
    list-style: none;

    li {
        margin-bottom: $spacing-unit-div-1-half;
    }
}

.post-year {
    margin-top: 2rem;
    margin-bottom: 0.25rem;
    font-weight: 700;
    @include relative-font-size(1.5);
    // color: lighten(var(--text-color), 15%);

    @media (max-width: 500px) {
        @include relative-font-size(1.2);
    }

   
}

.post-meta {
    display: inline-block;
    min-width: $padding-unit;

    @media (max-width: 500px) {
        @include relative-font-size(0.8);
    }

    @include relative-font-size(1);
    // color: lighten(var(--text-color), 25%);
}

.post-date {
    display: inline-block;
    min-width: 80px;

    @media (max-width: 500px) {
        @include relative-font-size(0.8);
    }

    @include relative-font-size(1);
}

.post-link-layout {
    margin-left: $padding-big;
    display: inline;

    @include relative-font-size(1.2);

    @media (max-width: 500px) {

        margin-left: 10px;
        @include relative-font-size(1);
    }

    
}

.black-link {
    color: var(--text-color);
    text-decoration: none;

    &:hover {
        color: var(--text-color);
        text-decoration: underline;
    }
}

.post-back {
    margin-bottom: $spacing-unit-mobile;
}

/**
 * Posts
 */
.post-header {
    margin-bottom: $spacing-unit;
}

.post-title,
.post-content h1 {
    line-height: 1.3;
    font-weight: 500;
    margin-top: -0.5rem;

    @include relative-font-size(2.2);
    
    @media (max-width: 500px) {
        @include relative-font-size(1.8);
    }

    @media screen and (min-width: $on-large) {
        @include relative-font-size(2.2);
    }
}

.post-content {
    margin-top: 3.2rem;
    margin-bottom: $spacing-unit;

    h2 {
        @include relative-font-size(1.75);

        @media screen and (min-width: $on-large) {
            @include relative-font-size(2);
        }
    }

    h3 {
        @include relative-font-size(1.375);

        @media screen and (min-width: $on-large) {
            @include relative-font-size(1.625);
        }
    }

    h4 {
        @include relative-font-size(1.125);

        @media screen and (min-width: $on-large) {
            @include relative-font-size(1.25);
        }
    }
}